.header-container{display:flex;justify-content:space-between}.header-container img{width:125px;height:125px;cursor:pointer}.header-container .header-center{display:flex;justify-content:center;align-items:center}.header-container .header-center div{display:flex;justify-content:center;align-items:center;margin-left:80px}.header-container .header-center img{width:32px}.header-container .header-center a{margin-left:10px;text-decoration:none;color:#6cbcc3;font-size:24px}@media (max-width: 768px), (min-width: 768px) and (max-width: 1023px){.header-container{flex-direction:column}.header-container .header-center{flex-direction:column;align-items:baseline}.header-container .header-center div{margin-left:0px}}

