.property-info-container{border:1px solid #428bff;padding:15px 30px 5px 30px;border-radius:5px;width:400px}

.estimate-table{border-collapse:collapse;background:white;width:100%;margin-bottom:20px;text-align:left;border-radius:5px;overflow:hidden;box-shadow:0 30px 40px rgba(0,0,0,0.15);-webkit-box-shadow:0 30px 40px rgba(0,0,0,0.15);-moz-box-shadow:0 30px 40px rgba(0,0,0,0.15);font-family:"Roboto", sans-serif}.estimate-table tr{height:50px;cursor:default}.estimate-table thead{background-color:#428bff}.estimate-table thead th{font-size:18px;color:#fff;line-height:1.6;font-weight:400;background:rgba(0,0,0,0.25);padding-left:10px}.estimate-table thead th:nth-child(1){width:30%}.estimate-table tbody tr{color:#303236;border-bottom:1px solid #ececec}.estimate-table tbody tr:hover{background-color:#ececec}.estimate-table tbody td{font-size:16px}@media (min-width: 1024px){.estimate-table tbody td{padding-left:10px}.estimate-table tbody td:nth-child(1){padding-left:20px}}@media (max-width: 768px), (min-width: 768px) and (max-width: 1023px){.estimate-table{display:block}.estimate-table thead,.estimate-table tbody,.estimate-table th,.estimate-table td,.estimate-table tr{display:block}.estimate-table thead tr{position:absolute;top:-9999px;left:-9999px}.estimate-table tr{height:100%;padding:20px 10px}.estimate-table td{border:none;position:relative;padding-left:50%;margin-bottom:6px}.estimate-table td:before{position:absolute;top:6px;left:6px;width:45%;padding-right:10px;white-space:nowrap}.estimate-table td:nth-of-type(2):before{content:"Darbų kaina, €"}.estimate-table td:nth-of-type(3):before{content:"Kiekis";padding-top:8px}.estimate-table td:nth-of-type(4):before{content:"Mato vienetas"}.estimate-table td:nth-of-type(5):before{content:"Suma, €"}}

.react-tabs__tab-list{margin:20px 0px;padding:6px;display:flex;align-items:center;justify-content:center}.react-tabs__tab{display:flex;flex-direction:column;padding:12px 28px;color:#303236;font-weight:700;border:none;transition:0.2s}.react-tabs__tab:hover{background-color:#f7f7f7;border-radius:5px}.react-tabs__tab:focus{box-shadow:none}.react-tabs__tab img{width:48px;height:48px;margin:0 auto;margin-bottom:5px}.react-tabs__tab p{margin:0;text-align:center}.react-tabs__tab--selected{border:none;border-radius:0;color:#428bff}.react-tabs__tab--selected img{background-image:url(/Statybviete/static/media/bathtub-blue.a56c87ca.svg)}@media (max-width: 768px), (min-width: 768px) and (max-width: 1023px){.react-tabs__tab{padding:8px 16px}}@media (max-width: 480px){.react-tabs__tab-list{display:block}}

