.about-section {
  background: url(../../assets/images/interior.jpg) no-repeat left;
  background-size: 55%;
  background-color: #fdfdfd;
  overflow: hidden;
  padding: 100px 0;

  .inner-container {
    width: 55%;
    float: right;
    background-color: #fdfdfd;
    padding: 150px;

    h1 {
      margin-bottom: 30px;
      font-size: 30px;
      font-weight: 900;
    }

    .text {
      font-size: 13px;
      color: #545454;
      line-height: 30px;
      text-align: justify;
      margin-bottom: 40px;
    }

    .skills {
      display: flex;
      justify-content: space-between;
      font-weight: 700;
      font-size: 13px;
      margin-top: 12px;

      span {
        width: 33%;
        padding-right: 14px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .about-section .inner-container {
    padding: 80px;
  }
}

@media screen and (max-width: 1000px) {
  .about-section {
    background-size: 100%;
    padding: 100px 40px;

    .inner-container {
      width: 100%;
    }
  }
}

@media screen and (max-width: 600px) {
  .about-section {
    padding: 75px 30px;

    .inner-container {
      padding: 60px;

      span {
        padding-right: 10px;
      }
    }
  }
}
